      const canvas = await html2canvas(quotationElement, { scrollY: -window.scrollY });

      // Initialize jsPDF with A4 size
      const pdf = new JsPDF('p', 'mm', 'a4');

      // Calculate the height of the content on A4 size page
      const contentHeight = canvas.height * 210 / canvas.width; // A4 width is 210mm

      // Add the captured content to the PDF
      pdf.addImage(canvas, 'PNG', 0, 0, 210, contentHeight);

